@import "../../../../scss/Colors";
@import "../../../../scss/index.scss";

.fullsize {
  width: 100%;
  height: 100%;

  & h1 {
    font-style: oblique;
    font-weight: bold;
    font-size: 3.75rem;
    text-align: center;
    color: #fff;
    margin-top: 2%;
  }

  & h2 {
    font-size: 3rem;
  }

  & h3 {
    margin: 20px;
    color: #fff !important;
  }
}

a {
  font-weight: bold;
  color: $ml_blue;

  &:hover {
    color: $ml_red;
    text-decoration: none;
  }
}

.feed {
  width: 100%
}

.instagram {
  background-color: #fff;
  padding: 20px;
  width: 100%;
}

.facebookFeed {
  border: none;
  overflow: hidden;
  background-color: transparent;
}

iframe {
  width: 45% !important;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 608px) {
  .feed {
    width: 50%;
  }
}
