body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #3e3936 !important;
  padding-top: 90px !important; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.fullsize {
  width: 100%;
  height: 100%; }

.navbar-toggler {
  border: 1px solid #fff;
  background-color: #29aae1; }
  .navbar-toggler-icon {
    background: #fff url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='rgba(0, 0, 0, 0.5)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e"); }

.navbar-collapse {
  background-color: #3e3936;
  padding: 10px;
  border-radius: 5px; }

.ml_submit {
  background-color: #29aae1; }
  .ml_submit:hover {
    background-color: #ef3332; }

.fullsize {
  width: 100%;
  height: 100%; }
  .fullsize h1 {
    font-style: oblique;
    font-weight: bold;
    font-size: 3.75rem;
    text-align: center;
    color: #fff;
    margin-top: 2%; }
  .fullsize h2 {
    font-size: 3rem; }
  .fullsize h3 {
    margin: 20px;
