a {
  font-weight: bold;
  color: #29aae1; }
  a:hover {
    color: #ef3332;
    text-decoration: none; }

.feed {
  width: 100%; }

.instagram {
  background-color: #fff;
  padding: 20px;
  width: 100%; }

.facebookFeed {
  border: none;
  overflow: hidden;
  background-color: transparent; }

iframe {
  width: 45% !important;
  margin-left: auto;
  margin-right: auto; }

@media (max-width: 608px) {
  .feed {
    width: 50%; } }
